<template>
  <q-page padding class="container">
    <q-card
      flat
      bordered
      class="rounded"
      :class="$q.screen.gt.sm ? 'q-px-lg q-py-sm' : 'q-pa-md'"
    >
      <q-toolbar class="q-pa-none">
        <q-toolbar-title>
          <div class="font-medium text-h5">{{ title }}</div>
        </q-toolbar-title>
      </q-toolbar>
    </q-card>

    <div class="rounded q-my-lg">
      <FileUploader
        multiple
        ref="filesUploader"
        :removedFile="removedFile"
        :filter="filesChecker"
        @rejected="onRejected"
        @added="filesAddedQued"
        max-file-size="5000000"
      />
    </div>

    <q-card flat bordered>
      <q-card-section :class="$q.screen.gt.sm ? 'q-px-lg q-py-lg' : 'q-pa-md'">
        <q-toolbar class="text-primary q-pa-none">
          <q-toolbar-title class="text-h6 font-medium">
            Attached files
            <div class="text-body2 font-normal color-primary2 q-pt-xs">
              Files and assets that have been attached to this project.
            </div>
          </q-toolbar-title>
        </q-toolbar>
      </q-card-section>

      <q-separator />

      <q-card-section
        :class="$q.screen.gt.sm ? 'q-px-lg' : 'q-px-md'"
        class="row q-col-gutter-md items-center justify-between q-py-md"
      >
        <!-- Top Left -->
        <div class="col-12 col-md-6">
          <q-btn-group
            :spread="$q.screen.sm || $q.screen.lt.md"
            class="rounded no-shadow border-add"
          >
            <q-btn
              flat
              no-caps
              class="hover-text-secondary"
              :color="ownerFilter === '' ? 'secondary' : 'primary'"
              :class="ownerFilter === '' ? 'bg-accent' : ''"
              @click="ownerFilter = ''"
            >
              <span class="text-body2 font-medium">View All</span>
            </q-btn>
            <q-separator vertical />
            <q-btn
              flat
              no-caps
              class="hover-text-secondary"
              :color="ownerFilter === 'me' ? 'secondary' : 'primary'"
              :class="ownerFilter === 'me' ? 'bg-accent' : ''"
              @click="ownerFilter = 'me'"
            >
              <span class="text-body2 font-medium">Your Files</span>
            </q-btn>
            <q-separator vertical />
            <q-btn
              flat
              no-caps
              class="hover-text-secondary"
              :color="ownerFilter === 'others' ? 'secondary' : 'primary'"
              :class="ownerFilter === 'others' ? 'bg-accent' : ''"
              @click="ownerFilter = 'others'"
            >
              <span class="text-body2 font-medium">Shared Files</span>
            </q-btn>
          </q-btn-group>
        </div>

        <!-- Top Right -->
        <div class="col-12 col-md-6">
          <div class="row q-col-gutter-md items-center justify-end text-right">
            <div class="col-12 col-sm-6 col-md-auto">
              <q-input
                v-model="searchQuery"
                dense
                outlined
                clearable
                debounce="500"
                class="small-field"
                input-class="text-body1 font-medium text-primary"
                placeholder="Search for files"
                clear-icon="icon-x-close"
              >
                <template v-slot:prepend>
                  <q-icon name="icon-search-sm" class="hover-text-secondary" />
                </template>
              </q-input>
            </div>
            <div class="col-12 col-sm-6 col-md-auto">
              <q-select
                dense
                multiple
                outlined
                emit-value
                map-options
                color="primary"
                hide-dropdown-icon
                :options="columns"
                :options-dense="false"
                :options-cover="true"
                v-model="visibleColumns"
                display-value="Filters"
                :option-value="(val, row) => (val ? val.name : null)"
                class="small-field hover-text-secondary table-select-filter"
                popup-content-class="shadow-ui"
                transition-show="jump-up"
                transition-hide="jump-down"
                style="min-width: 100px"
              >
                <template v-slot:prepend>
                  <q-icon
                    name="icon-filter-lines"
                    size="sm"
                    color="primary"
                    class="hover-text-secondary"
                  />
                </template>
                <template v-slot:option="scope">
                  <q-item
                    v-bind="scope.itemProps"
                    :disable="scope.opt.required"
                    class="hover-bg-accent"
                  >
                    <q-item-section>
                      <q-item-label
                        :class="
                          scope.opt.required || scope.selected
                            ? 'text-secondary'
                            : ''
                        "
                      >
                        {{ scope.opt.label }}
                      </q-item-label>
                    </q-item-section>
                  </q-item>
                </template>
              </q-select>
            </div>
          </div>
        </div>
      </q-card-section>

      <q-separator />

      <q-table
        row-key="id"
        filteredSortedRows
        binary-state-sort
        :rows="orgFiles"
        :columns="columns"
        color="primary"
        :dense="tableDense"
        :loading="tableLoading"
        rows-per-page-label="Files per page"
        no-results-label="The filter didn't uncover any results."
        no-data-label="I didn't find anything for you."
        loading-label="Getting data from the server."
        :rows-per-page-options="[10, 20, 30, 50, 50, 0]"
        style="max-height: 950px"
        selection="multiple"
        v-model:pagination="pagination"
        v-model:selected="selectedFiles"
        :visible-columns="visibleColumns"
        @request="onRequest"
      >
        <!-- Loading -->
        <template v-slot:loading>
          <div
            class="bg-color absolute-top"
            :style="`top: ${tableDense ? '50px' : '56px'}`"
          >
            <q-linear-progress size="2px" indeterminate color="secondary" />
          </div>
        </template>

        <!-- Table Header Slot -->
        <template v-slot:header="scope">
          <q-tr class="bg-color">
            <q-th
              auto-width
              style="position: sticky; top: 0; z-index: 2; padding-right: 0"
              class="cursor-pointer hover-text-secondary text-left"
              @click.stop="scope.selected = !scope.selected"
            >
              <q-btn
                flat
                round
                size="md"
                :icon="scope.selected ? 'icon-check-square' : 'icon-square'"
                :color="
                  scope.selected || selectedFiles.length
                    ? 'secondary'
                    : 'grey-5'
                "
              />
            </q-th>
            <q-th
              v-for="(col, i) in scope.cols"
              :key="col.name"
              :props="scope"
              :auto-width="i !== 0"
              style="position: sticky; top: 0; z-index: 2"
              :style="i === 0 ? 'padding-left: 0' : ''"
            >
              <span class="text-body2 font-medium color-primary2">
                {{ col.label }}
              </span>
            </q-th>
            <!-- Files Options -->
            <q-th
              auto-width
              class="text-right"
              style="position: sticky; top: 0; z-index: 1"
            >
              <span class="text-body2 font-medium text-transparent">
                Options
              </span>
            </q-th>
          </q-tr>
        </template>

        <!-- Table Body Slot -->
        <template v-slot:body="scope">
          <q-tr
            :props="scope"
            class="hover-bg-accent"
            :class="scope.selected ? 'bg-accent' : 'bg-transparent'"
          >
            <q-td
              :class="scope.selected ? 'bg-accent' : 'bg-transparent'"
              class="cursor-pointer text-primary text-left hover-text-secondary"
              style="padding-right: 0"
              @click.stop="scope.selected = !scope.selected"
            >
              <q-btn
                flat
                round
                size="md"
                :icon="scope.selected ? 'icon-check-square' : 'icon-square'"
                :color="scope.selected ? 'secondary' : 'grey-5'"
              />
            </q-td>
            <q-td
              v-for="(_r, i) in scope.cols"
              :key="_r.name || i"
              :props="scope"
              :class="scope.selected ? 'bg-accent' : 'bg-transparent'"
              class="cursor-pointer text-primary"
              :style="i === 0 ? 'padding-left: 0' : ''"
            >
              <!-- Filename with icon -->
              <q-item v-if="i === 0" class="text-body2 q-pa-none">
                <q-item-section avatar style="min-width: unset" class="q-pr-sm">
                  <CardIconBox
                    :icon="`icon-${scope.row?.folder ? 'folder' : 'image-03'}`"
                  />
                </q-item-section>
                <q-item-section>
                  <span :class="i === 0 ? 'font-medium' : ''">
                    {{ _r.value }}
                  </span>
                </q-item-section>
              </q-item>
              <!-- File Info -->
              <span
                v-else
                class="text-body2 color-primary2"
                :class="i === 0 ? 'font-medium' : ''"
              >
                {{ _r.value }}
              </span>
            </q-td>
            <q-td
              :class="scope.selected ? 'bg-accent' : 'bg-transparent'"
              style="
                padding-right: 24px;
                padding-top: 18px;
                padding-bottom: 18px;
              "
            >
              <div class="q-gutter-sm text-right">
                <q-btn
                  flat
                  round
                  size="12px"
                  color="grey-7"
                  icon="icon-trash-01"
                  class="hover-text-negative hover-bg-accent"
                  :disable="tableLoading"
                  @click="deleteOrgFilesLocally(scope?.row)"
                />

                <q-btn
                  flat
                  round
                  size="12px"
                  color="grey-7"
                  icon="icon-edit-01"
                  class="hover-text-secondary hover-bg-accent"
                  :disable="tableLoading"
                />
              </div>
            </q-td>
          </q-tr>
        </template>

        <!-- Bottom -->
        <template v-slot:bottom="scope">
          <div
            v-if="$q.screen.lt.xs || $q.screen.xs"
            class="q-py-md row full-width items-center justify-center q-col-gutter-sm q-virtual-scroll--skip"
          >
            <!-- Pagination -->
            <q-pagination
              unelevated
              boundary-numbers
              v-model="pagination.page"
              :disable="tableLoading"
              color="text-grey-6"
              class="color-primary2 rounded"
              active-color="grey-2"
              active-text-color="primary"
              :max="scope.pagesNumber"
              :max-pages="6"
            />
          </div>
          <div
            class="q-py-md row full-width items-center justify-between q-col-gutter-sm q-virtual-scroll--skip"
          >
            <!-- Previous Page -->
            <div class="text-left" :class="$q.screen.gt.xs ? 'col-2' : 'col-6'">
              <q-btn
                dense
                outline
                no-caps
                :disable="scope.isFirstPage"
                @click="scope.prevPage"
                class="rounded q-px-md slide-hover"
                :class="
                  scope.isFirstPage
                    ? 'color-primary2'
                    : 'text-primary hover-text-secondary'
                "
              >
                <q-icon
                  size="xs"
                  name="icon-arrow-narrow-left"
                  :class="!scope.isFirstPage ? 'slide-hover-right-md' : ''"
                />
                <span class="q-pl-sm">Previous</span>
              </q-btn>
            </div>

            <!-- Pagination -->
            <q-pagination
              v-if="$q.screen.gt.xs"
              unelevated
              boundary-numbers
              v-model="pagination.page"
              :disable="tableLoading"
              color="text-grey-6"
              class="color-primary2 rounded"
              active-color="grey-2"
              active-text-color="primary"
              :max="scope.pagesNumber"
              :max-pages="6"
            />

            <!-- Next Page -->
            <div
              class="text-right"
              :class="$q.screen.gt.xs ? 'col-2' : 'col-6'"
            >
              <q-btn
                dense
                outline
                no-caps
                :disable="scope.isLastPage"
                @click="scope.nextPage"
                class="rounded q-px-md slide-hover"
                :class="
                  scope.isLastPage
                    ? 'color-primary2'
                    : 'text-primary hover-text-secondary'
                "
              >
                <span class="q-pr-sm">Next</span>
                <q-icon
                  size="xs"
                  name="icon-arrow-narrow-right"
                  :class="!scope.isLastPage ? 'slide-hover-left-md' : ''"
                />
              </q-btn>
            </div>
          </div>
        </template>

        <!-- No Data -->
        <template v-slot:no-data="{ icon, message, filter }">
          <div
            class="q-py-md full-width row flex-center color-primary2 q-gutter-sm"
          >
            <q-icon
              size="sm"
              :color="tableLoading ? 'secondary' : 'negative'"
              :name="
                tableLoading
                  ? 'icon-download-cloud-01'
                  : filter
                  ? 'icon-filter-funnel-01'
                  : icon
              "
            />
            <span class="text-body2 color-primary2 font-medium">
              {{ message }}
            </span>
          </div>
        </template>
      </q-table>
    </q-card>
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import { date, debounce, format, uid } from 'quasar';
import {
  checkIfObject,
  searchInObjectArray,
} from 'src/assets/scripts/functions';
import FileUploader from 'src/components/Helpers/FilesUploader.vue';
const { humanStorageSize } = format;

export default defineComponent({
  name: 'FilesPage',

  components: { CardIconBox, FileUploader },

  data() {
    return {
      title: 'Files and Assets',
      searchQuery: '',
      ownerFilter: '',
      tableLoading: false,
      removedFile: null,
      tableDense: true,
      selectedFiles: [],
      visibleColumns: [],
      onRequestDelayed: null,
      orgFiles: [],
      pagination: {
        descending: true,
        page: 1,
        rowsPerPage: 10,
        rowsNumber: 0,
      },
      columns: [
        {
          name: 'filename',
          required: true,
          label: 'Filename',
          align: 'left',
          field: (row) => row.filename,
          format: (val) => `${val}`,
          sortable: true,
        },
        {
          name: 'id',
          label: 'ID',
          align: 'left',
          field: (row) => row.id,
          format: (val) => `${val}`,
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
        {
          name: 'filesize',
          label: 'File size',
          align: 'left',
          field: (row) => row.filesize,
          format: (val) => `${humanStorageSize(val)}`,
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
        {
          name: 'date_uploaded',
          label: 'Date uploaded',
          align: 'left',
          field: (row) => row.date_uploaded,
          format: (val) => `${date.formatDate(val, 'MMM D, YYYY')}`,
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
        {
          name: 'last_updated',
          label: 'Last updated',
          align: 'left',
          field: (row) => row.last_updated,
          format: (val) => `${date.formatDate(val, 'MMM D, YYYY')}`,
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
        {
          name: 'uploaded_by',
          label: 'Uploaded by',
          align: 'left',
          field: (row) => row?.uploaded_by,
          format: (val) =>
            `${this.selectedOrgUsers?.[val]?.firstname} ${this.selectedOrgUsers?.[val]?.lastname}`,
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
      ],
    };
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    this.visibleColumns = this.columns
      .map((c) => c.name) // Get all the column names
      .filter((e) => !['id'].includes(e)); // Hide some columns initially

    this.onRequestDelayed = debounce(this.onRequest, 1000);
    this.onRequest({
      pagination: this.pagination,
      filter: this.filter,
    });
  },

  computed: {
    ...mapState(useUser, ['user', 'selectedOrgFiles', 'selectedOrgUsers']),

    orgFilesOriginal() {
      return Object.values(this.selectedOrgFiles);
    },
  },

  watch: {
    searchQuery: {
      async handler(val) {
        this.onRequest(
          {
            pagination: this.pagination,
            filter: { search: val, owner: this.ownerFilter },
          },
          1000
        );
      },
    },
    ownerFilter: {
      async handler(val) {
        this.onRequest(
          {
            pagination: this.pagination,
            filter: { search: this.searchQuery, owner: this.ownerFilter },
          },
          1000
        );
      },
    },
    orgFilesOriginal: {
      async handler(val) {
        this.onRequest(
          {
            pagination: this.pagination,
            filter: { search: this.searchQuery, owner: this.ownerFilter },
          },
          1000
        );
      },
      deep: true,
    },
    pagination: {
      async handler(val) {
        this.onRequest(
          {
            pagination: val,
            filter: { search: this.searchQuery, owner: this.ownerFilter },
          },
          1000
        );
      },
      deep: true,
    },
  },

  methods: {
    ...mapActions(useUser, ['deleteOrgFiles', 'addOrgFiles']),

    deleteOrgFilesLocally(file) {
      if (file?.file) {
        this.removedFile = file?.file;
        this.$refs?.['filesUploader']?.removeFile?.(file?.file);
      }
      this.$q.notify({
        spinner: true,
        message: `Removing ${file.filename}.`,
        spinnerColor: 'negative',
        timeout: 1500,
      });
      this.deleteOrgFiles(file.id);
    },

    filesChecker(
      files,
      fileTypes = ['image/svg+xml', 'image/png', 'image/jpeg', 'image/gif']
    ) {
      return files.filter((file) => {
        let toUp = fileTypes.includes(file.type);
        return toUp;
      });
    },

    onRejected(rejectedEntries) {
      let duplicateFiles = rejectedEntries?.filter(
        (file) => file.failedPropValidation === 'duplicate'
      );
      let duplicateFilesCount = duplicateFiles.length;
      let nonDuplicateFiles = rejectedEntries.length - duplicateFilesCount;
      let message = `${rejectedEntries.length} file${
        rejectedEntries.length > 1 ? 's' : ''
      } did not pass validation constraints`;
      switch (true) {
        case duplicateFilesCount == rejectedEntries.length:
          message = `File${duplicateFilesCount > 1 ? 's' : ''} already exist.`;
          break;

        case duplicateFilesCount > 0:
          message = `${duplicateFilesCount} duplicate file${
            duplicateFilesCount > 1 ? 's' : ''
          } and ${nonDuplicateFiles} file${
            nonDuplicateFiles > 1 ? 's' : ''
          } did not pass validation constraints.`;
          break;

        default:
          break;
      }
      this.$q.notify({
        message,
        iconColor: 'negative',
        icon: 'icon-alert-triangle',
      });
    },

    filesAddedQued(files) {
      files.forEach((file) => {
        let file_id = uid();
        let date_uploaded = new Date();
        this.addOrgFiles(file_id, {
          id: file_id,
          uid: file_id,
          filename: file.name,
          folder: false,
          filesize: file.size,
          date_uploaded: date_uploaded,
          last_updated: date_uploaded,
          uploaded_by: this.user?.id,
          file: file,
        });
        if (files.length === 1)
          // Notify filename for single upload
          this.$q.notify({
            spinner: true,
            message: `Uploading ${file.name}.`,
            timeout: 1500,
          });
      });
      // Notify total upload for multi upload
      if (files.length > 1)
        this.$q.notify({
          spinner: true,
          message: `Uploading ${files.length} files.`,
          timeout: 2000,
        });
    },

    triggerUpload() {
      this.$refs['uploadBtnTrigger']?.click();
    },

    filterMethod(dataArray, filter) {
      return filter && checkIfObject(filter) && Object.values(filter).join('')
        ? (filter?.search
            ? searchInObjectArray(dataArray, filter?.search, [
                'filename',
                'uploaded_by',
              ])
            : dataArray
          ).filter((data) => {
            let res = true;
            // Filter Owner
            if (filter?.owner)
              switch (filter?.owner) {
                case 'me':
                  res = data.uploaded_by === this.user?.id;
                  break;
                case 'others':
                  res = data.uploaded_by !== this.user?.id;
                  break;
                default:
                  break;
              }
            return res;
          })
        : dataArray.slice();
    },

    // Search
    fetchFromServer(startRow, count, filter, sortBy, descending) {
      const data = this.filterMethod(this.orgFilesOriginal, filter);

      // handle sortBy
      if (sortBy) {
        const sortFn =
          sortBy === 'desc'
            ? descending
              ? (a, b) => (a.name > b.name ? -1 : a.name < b.name ? 1 : 0)
              : (a, b) => (a.name > b.name ? 1 : a.name < b.name ? -1 : 0)
            : descending
            ? (a, b) => parseFloat(b[sortBy]) - parseFloat(a[sortBy])
            : (a, b) => parseFloat(a[sortBy]) - parseFloat(b[sortBy]);
        data.sort(sortFn);
      }

      return data.slice(startRow, startRow + count);
    },

    getRowsNumberCount(filter) {
      if (
        !filter ||
        !(checkIfObject(filter) && Object.values(filter).join(''))
      ) {
        return this.orgFilesOriginal.length;
      }
      let count = 0;
      this.filterMethod(this.orgFilesOriginal, filter).forEach((e) => {
        count++;
      });
      return count;
    },

    onRequest(props) {
      const { page, rowsPerPage, sortBy, descending } = props.pagination;
      const filter = props.filter;

      this.tableLoading = true;

      // emulate server
      setTimeout(() => {
        // update rowsCount with appropriate value
        this.pagination.rowsNumber = this.getRowsNumberCount(filter);

        // get all rows if "All" (0) is selected
        const fetchCount =
          rowsPerPage === 0 ? this.pagination.rowsNumber : rowsPerPage;

        // calculate starting row of data
        const startRow = (page - 1) * rowsPerPage;

        // fetch data from "server"
        const returnedData = this.fetchFromServer(
          startRow,
          fetchCount,
          filter,
          sortBy,
          descending
        );

        // clear out existing data and add new
        this.orgFiles.splice(0, this.orgFiles.length, ...returnedData);

        // don't forget to update local pagination object
        this.pagination.page = page;
        this.pagination.rowsPerPage = rowsPerPage;
        this.pagination.sortBy = sortBy;
        this.pagination.descending = descending;

        // ...and turn of loading indicator
        this.tableLoading = false;
      }, 750);
    },
  },
});
</script>
